// Variables
$grey: #7d889e;
$dark-navy: #1f314f;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  height: 100vh;
  background-color: #d5e1ef;
  font-size: 1.5rem;
  font-family: "Outfit", sans-serif;
  padding-top: 15.2rem;
}

img {
  width: 100%;
  height: auto;
}

.attribution {
  font-size: 11px;
  text-align: center;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

.qr-code-component {
  width: 32rem;
  margin: 0 auto;
  background-color: #fff;
  color: $grey;
  padding: 1.6rem 1.6rem 4rem 1.6rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 25px 25px 0 rgba(0, 0, 0, 0.0477);

  .img-container {
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 2.4rem;
    width: 28.8rem;
    height: 28.8rem;
  }
  h1 {
    color: $dark-navy;
    font-size: 2.2rem;
    margin-bottom: 1.6rem;
    font-weight: 700;
  }

  p {
    font-size: 1.5rem;
    font-weight: 300;
  }
}
